import { Injectable } from '@angular/core';
import { HttpHandler, HttpInterceptor, HttpRequest, HttpResponse } from '@angular/common/http';
import { of,  Observable, switchMap } from 'rxjs';
import { postOnly } from './utils';
import { AppState } from '../core/app.service';


@Injectable()
export class DemoInterceptor implements HttpInterceptor {
    constructor(
        private appState: AppState,
    ) {}

    @postOnly
    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<any> {
        return next.handle(req).pipe(
            switchMap(event => {
                if (event instanceof HttpResponse) {
                    const license = event.body.data ? event.body.data.license : event.body.license;
                    if (license && license.demo) {
                        this.appState.demo = true;
                    }
                }
                return of(event);
            }),
        );
    }
}
