import { CsfComponent } from 'app/utils/csf/csf.component';
import { I360LogWatcherModule } from 'app/components/settings/log-watcher/log-watcher.module';
import { EllipsisComponent } from 'app/utils/ellipsis.component';
import { ColoredIpComponent } from 'app/utils/colored-ip/colored-ip.component';
import { MatRippleModule } from '@angular/material/core';
import { I360ModalModule } from 'app/utils/modals/modal.module';
import { MatDialogModule } from '@angular/material/dialog';
import { MatMenuModule } from '@angular/material/menu';
import { MatTooltipModule } from '@angular/material/tooltip';
import { I360NavigationTabsModule } from 'app/components/navigation-tabs/navigation-tabs.module';
