import { I360RefresherModule } from 'app/utils/refresher/refresher.module';
import { MatIconModule } from '@angular/material/icon';
import { I360ClockModule } from 'app/utils/clock/clock.module';
import { I360MalwareModule } from 'app/components/i360-malware/i360-malware.module';
import { I360NavigationModule } from 'app/utils/navigation/navigation.module';
import { I360NotificationsModule } from 'app/components/notification/notifications.module';
import { I360SettingsModule } from 'app/components/settings/settings.module';
import { I360DescriptionModule } from 'app/utils/description/description.module';
import { I360ConfigModule } from 'app/utils/config/config.module';
import { angular18894CrutchProvider } from 'app/core/misc';
import { XHR } from 'app/utils/xhr/xhr';
