import { I360ButtonModule } from 'app/utils/button/button.module';
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
import { TextInputModule } from 'app/components/text-input/text-input.module';
import { I360ExperimentalModule } from 'app/utils/experimental/experimental.module';
import { I360LogBoxModule } from 'app/utils/log-box/log-box.module';
import { StatisticsService } from 'app/services/statistics';
import { I360AddItemModule } from '../utils/add-item/add-item.module';

const declarations = [
    ComingSoonComponent,
    TimestampPipe,
    CheckboxGroupDirective,
    InlineComponent,
    CsfComponent,
    ColoredIpComponent,
    // ExtendMatChipDirective,
    EllipsisComponent,
    AppComponent,
];

const modules = [
    MatRippleModule,
    MatSlideToggleModule,
    MatCheckboxModule,
    MatRadioModule,
    MatProgressBarModule,
    RouterModule,
    CommonModule,
    MatSliderModule, // after BrowserModule, see https://github.com/angular/material2/issues/4278
    FormsModule,
    HttpClientModule,
    ReactiveFormsModule,
    I360AddItemModule,
    I360LoaderModule,
    I360TableModule,
    I360ValidationModule,
    MatSelectModule,
    MatAutocompleteModule,
    MatButtonModule,
    I360ModalModule,
    MatDialogModule,
    MatIconModule,
    MatMenuModule,
    MatChipsModule,
    MatTooltipModule,
    I360TranslateModule,
    I360NavigationTabsModule,
    I360MalwareModule,
    I360LogWatcherModule,
    I360WarningMessageModule,
    I360CircleProgressModule,
    I360RefresherModule,
    I360ClockModule,
    I360NavigationModule,
    I360SettingsModule,
    I360DescriptionModule,
    I360ConfigModule,
    I360ExperimentalModule,
    I360MenuProfileModule,
    I360ButtonModule,
    MatFormFieldModule,
    MatInputModule,
    TextInputModule,
    I360LogBoxModule,
];

@NgModule({
    exports: [
        I360NotificationsModule,
        ...declarations,
        ...modules,
    ],
    imports: [
        ...modules,
        I360NotificationsModule,
    ],
    declarations: declarations,
})
export class SharedModule {
    static forUser(): ModuleWithProviders<SharedModule> {
        return {
            ngModule: SharedModule,
            providers: [
                angular18894CrutchProvider,
                SettingsService,
                XHR,
                FileService,
                BackupSystemsService,
                // LicenseService should be here to allow LicenseInterceptor
                // interrupt register request
                LicenseService,
                StatisticsService,
                { provide: HTTP_INTERCEPTORS, useClass: DemoInterceptor, multi: true},
                { provide: HTTP_INTERCEPTORS, useClass: SessionExpiredInterceptor, multi: true},
            ],
        };
    }
    // it is needed because service is not instantiated if there is no usage
    // optional - this service is declared in 'forUser' method, so will be present not
    // in all imports of shared module
    // this functionality is covered by tests
    constructor(
        @Optional() statistics: StatisticsService,
    ) {}
}
